.currencyChartWrapper {
  width: 100%;

  .optionsBlock {
    display: flex;
    justify-content: space-evenly;

    .selectBlock {
      display: flex;
      align-items: center;

      label {
        width: max-content;
        font-weight: bold;
        margin-right: 20px;
      }

      .select {
        width: 100px;
      }
    }

    @media screen and (max-width: 500px) {
      font-size: 12px;
      .selectBlock {
        label {
          margin-right: 5px;
        }
      }
    }
  }
}